import React from 'react';
import Media from 'react-media';
import style from './fourth-screen.module.css';
import Header from './header/header';
import NavigationServices from '../navigationServices/navigationServices';
import Information from './infornation/information';
import imgOffice from '../../img/infoImg/CompanyOffice.png';
import imgOfficeW from '../../img/infoImg/CompanyOffice.webp';

const FourthSection = ({ onOpenModal }) => (
  <section id="CompanyOffice" name="CompanyOffice" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={3} />
          </div>
        )
      }
    </Media>
    <Information onOpenModal={onOpenModal} />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={imgOfficeW} type="image/webp" />
            <source srcSet={imgOffice} type="image/png" />
            <img className={style.img} src={imgOffice} alt="Portal" />
          </picture>
        )
      }
    </Media>
  </section>
);

export default FourthSection;
