import React from 'react';
import Media from 'react-media';
import style from './information.module.css';
import Buttons from '../../buttons/buttons-light';
import img from '../../../img/infoImg/CompanyOffice.png';
import imgW from '../../../img/infoImg/CompanyOffice.webp';

const Information = ({ onOpenModal }) => (
  <div className={style.wrapper}>
    <p className={style.text}>
      Юридически-грамотный инструмент работы с юр. лицами в электронном виде.
      Юридическая и финансовая информация, договора, передача показаний приборов
      учета, расчет начислений, печать счетов и актов, оплата через любой банк
    </p>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={imgW} type="image/webp" />
            <source srcSet={img} type="image/png" />
            <img className={style.img} src={img} alt="Portal" />
          </picture>
        )
      }
    </Media>
    <Buttons
      path="officeCompany"
      onOpenModal={onOpenModal}
      className={style.buttons}
    />
  </div>
);

export default Information;
